*,
*::before,
*::after {
  box-sizing: border-box;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.image {
  width: 100%;
  height: auto;
  margin: 0;
}

.picture {
  line-height: 0;
}

.br--mobile {
  display: none;
}
@media (max-width: 767px) {
  .br--mobile {
    display: block;
  }
}
.br--tablet {
  display: none;
}
@media (min-width: 768px) {
  .br--tablet {
    display: block;
  }
}

@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 700;
