.review-form__radio-mark {
  position: relative;
  margin-right: 16px;
}
.review-form__radio-mark::before {
  position: absolute;
  top: -10px;
  left: -45px;
  width: 46px;
  height: 46px;
  content: "";
  border-radius: 50%;
  background-color: #f2f2f2;
}
.review-form__radio-mark::after {
  position: absolute;
  top: 5px;
  left: -30px;
  display: none;
  width: 16px;
  height: 16px;
  content: "";
  border-radius: 50%;
  background-color: #333333;
}

input[type=radio]:checked + .review-form__radio-mark::after {
  display: block;
}

.review-form__checkbox-mark {
  position: relative;
  margin-right: 16px;
}
.review-form__checkbox-mark::before {
  position: absolute;
  top: -10px;
  left: -47px;
  width: 46px;
  height: 46px;
  content: "";
  border-radius: 4px;
  background-color: #f2f2f2;
}
.review-form__checkbox-mark::after {
  position: absolute;
  top: 5px;
  left: -32px;
  display: none;
  width: 22px;
  height: 16px;
  content: "";
  background-image: url("../img/tick.svg");
}

input[type=checkbox]:checked + .review-form__checkbox-mark::after {
  display: block;
}

.review-form__label--control:hover .review-form__radio-mark::before,
.review-form__label--control:hover .review-form__checkbox-mark::before {
  background-color: #e5e5e5;
}

.review-form__label--control:active .review-form__radio-mark::before,
.review-form__label--control:active .review-form__checkbox-mark::before {
  background-color: #d5d5d5;
}

.review-form__label--control:active .review-form__radio-mark::after,
.review-form__label--control:active .review-form__checkbox-mark::after {
